import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'dayNow'
})
export class DayNowPipe implements PipeTransform {

  transform(day: number): string {
    switch(day) {
      case 0:
        return '';
      case 1:
        return '';
      case 2:
        return '';
      case 3:
        return '';
      case 4:
        return '';
      case 5:
        return '';
      case 6:
        return '';
      default:
        return '';
    }
  }

}
